import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import util from 'preact-util';

import LineGraph from '../../components/lineGraph';

const initialState = {};

@observer
class Demand extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadAll() {
		const { demandStore } = this.props.stores;
		demandStore.generateData();

		const now = Math.floor(new Date().getTime() / 1000);
		this.setState({ now });
	}

	setWindpark = (e) => {
		const { windmillStore } = this.props.stores;
		const windpark = e.target.dataset.windpark;
		windmillStore.setCurrentWindparkName(windpark);
		this.loadAll();
	}

	componentDidMount() {
		this.loadAll();
	}

	render() {
		const { now } = this.state;
		const { demandStore, windmillStore } = this.props.stores;
		const { demands: demandData, demandAi: aiData } = demandStore;
		const { windparks, currentWindparkName } = windmillStore;

		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-4'>
						<h1><i class='fas fa-hand-holding-usd' /> Demand</h1>
					</div>
					<div class='col-8 pt-2'>
						<ul class='nav nav-pills float-right'>
						{windparks && windparks.map(wp => (
							<li class='nav-item'>
								<button
									class={`btn btn-link nav-link ${wp.name === currentWindparkName ? 'active' : ''}`}
									onClick={this.setWindpark}
									data-windpark={wp.name}
								>{wp.name}</button>
							</li>
						))}
						</ul>
					</div>
					<div class='col-12'>
						<LineGraph
							stores={this.props.stores}
							width={1200}
							height={500}
							showXTicks={1}
							xTicksVal={val => util.isoDate(val)}
							paddingTop={30}

							dataLeft={[demandData, aiData]}
							legendLeft={['Demand', 'AI prediction']}
							yMaxLeft={4000}
							yMinLeft={0}
							showYTicksLeft={1}
							yTicksLeft={['4TW', '3TW', '2TW', '1TW', '0TW']}
							unitLeft={['Mw', 'Mw']}
							avgWindowLeft={[1, 1]}
							smoothLeft={[1, 1]}

							marker={now}

						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Demand;
